import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Vant from 'vant';
import { Dialog } from 'vant';
Vue.use(Dialog);
import 'vant/lib/index.css';
import 'vant/lib/index.less';
Vue.use(Vant);

Vue.config.productionTip = false

import Icon from './assets/icon/myIcon/iconfont.css'

import http from './http'
Vue.prototype.$http = http;
new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')


window.useRem = true;
const baseSize = 22;
(function(doc, win) {
    setRem();
    let resizeEvt = "orientationchange" in window ? "orientationchange" : "resize",
        recalc =  function() { setRem(); };
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);
})(document, window);

function setRem() { //1920
    
    const scale = document.documentElement.clientHeight / 1080; //Math.min(scale,2)2
    
    document.documentElement.style.fontSize = baseSize * Math.min(scale, 5) + "px";
}